.member-layout-container {
    display: grid;
    grid: 10% auto 5% / 1fr;
    height: 100%;

    .header-bar {
        width: 100%;
        height: 100%;
        background-color: #3f51b5;        
        box-shadow: -5px -5px 10px 5px;
        display: grid;
        grid: 1fr / auto auto;
        .top-left {
            text-align: left;
            img {
                margin: 4px;
            }
        }
        .top-right {
            text-align: right;
            padding: 5px;
        }
    }

    .content {
        width: 100%;
        height: 100%;
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .footer-bar {
        background-color: #3f51b5;
        box-shadow:2px -5px 10px -5px;
    }
    
    .secondary-color {
        background-color: #3f51b5 !important;
        color: white !important;
        font-size: 1rem !important;
    }

    .text-block {
        display: block;
        width: 150px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

.member-login-container {
    display: grid;
    grid: auto auto / auto;
    padding: 10%;
    .top {
        margin: 10px;
    }
    .bottom {
        width: 50%;
        padding-left: 25%;
    }
}